      margin-right: 12px; }
  .filters__dropdown {
    top: calc(100% + 16px);
    left: 0; }
    @media only screen and (max-width: 768px) {
      .filters__dropdown {
        top: 52px; } }

.fdrop {
  position: absolute;
  width: 100%;
  display: none;
  z-index: 10;
  background: #EDF0F9;
  box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.2); }
  .fdrop--active {
    display: block; }
  .fdrop__close {
    padding: 0;
    outline: 0;
    border: none;
    cursor: pointer;
    background: none;
    position: absolute;
    right: 16px;
    top: 16px;
    width: 20px;
    height: 20px;
    display: block; }
    .fdrop__close img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain; }
  .fdrop__inner {
    padding: 32px 24px 16px; }
    @media only screen and (max-width: 768px) {
      .fdrop__inner {
        padding-left: 0;
        padding-right: 0; } }
  .fdrop__row {
    display: flex;
    flex-wrap: wrap;
    margin-left: 0 -16px; }
  .fdrop__item {
    flex-basis: 279px;
    max-width: 279px;
    padding: 0 16px;
    margin-bottom: 24px; }
    @media only screen and (max-width: 1247px) {
      .fdrop__item {
        max-width: 33.333%;
        width: 33.333%;
        flex-basis: 33.333%; } }
    @media only screen and (max-width: 768px) {
      .fdrop__item {
        max-width: 50%;
        width: 50%;
        flex-basis: 50%; } }
    @media only screen and (max-width: 600px) {
      .fdrop__item {
        max-width: 100%;
        width: 100%;
        flex-basis: 100%; } }
  .fdrop__title {
    font-weight: 450;
    font-size: 14px;
    line-height: 135%;
    color: #000000;
    margin-bottom: 16px; }
  .fdrop .vsb-main {
    width: 100%; }
    .fdrop .vsb-main button {
      position: relative;
      padding: 0 32px 0 12px !important;
      height: 30px !important;
      box-sizing: border-box;
      width: 100% !important; }
      .fdrop .vsb-main button .title {
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 135%;
        color: #878E97;
        margin: 0;
        display: block;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis; }
      .fdrop .vsb-main button .caret {
        width: 10px !important;
        height: 10px !important;
        position: absolute;
        right: 11px !important;
        top: 45% !important;
        transform: translateY(-50%);
        margin: 0 !important;
        border: none !important;
        background: url("../images/fdown.svg") center center no-repeat; }
      .fdrop .vsb-main button.not-empty .title {
        color: #000000;
        font-weight: 450; }
      .fdrop .vsb-main button.not-empty .caret {
        background-image: url("../images/fclear.svg");
        top: 50% !important; }
    .fdrop .vsb-main .multi li:not(.grouped-option)::after,
    .fdrop .vsb-main .multi li:not(.grouped-option)::before {
      display: inline-block;
      box-sizing: content-box; }
  .fdrop .vsb-menu {
    border-radius: 0; }
  .fdrop__range-slider {
    width: 100%; }
    .fdrop__range-slider #priceSlider {
      width: calc(100% - 14px);
      margin: 0 auto; }
  .fdrop__inputs {
    display: flex;
    justify-content: space-between;
    margin-top: 20px; }
  .fdrop__input {
    width: calc(50% - 11px);
    position: relative; }
    .fdrop__input input {
      width: 100%;
      height: 32px;
      background: #FFFFFF;
      border: 1px solid #DCE4EE;
      box-sizing: border-box;
      border-radius: 3px;
