      line-height: 135%;
      color: #000000;
      padding: 0 30px 0 12px; }
    .fdrop__input::after {
      content: "грн";
      display: block;
      position: absolute;
      right: 12px;
      top: 50%;
      transform: translateY(-50%);
      font-size: 12px;
      line-height: 135%;
      color: #878E97;
      user-select: none;
      pointer-events: none; }
